.content {}

.options {
  text-align: center;
}

.PatternDisplay {}

.PatternContainer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.PatternCanvas {
  display: block;
}

html {
  height: 100%;
  background-color: #444;
  color: #ddd;
}

#root {
  width: 100%;
  height: 100%;
  display: block;
  overflow: hidden;
}

body {
  padding: 0px;
  margin: 0px;
  width: 100vw;
  height: 100vh;
  display: block;
  overflow: hidden;
}

.Collapsible {
  width: 100%;

}

.CollapsibleInner {
  padding-left: 16px;
  padding-right: 16px;

}

.App {
  display: grid;
  width: 100%;
  height: 100%;
  border: none;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50px 50% auto;
  grid-gap: 10px;
  grid-template-areas:
    "header header"
    "pattern image"
    "pattern settings";

}

.header {
  font-size: 50px;
  font-weight: bolder;
  text-align: center;
  grid-area: header;
}

.pattern {
  grid-area: pattern;
  width: 100%;
  height: 100%;
}

.image {
  grid-area: image;
  width: 100%;
  height: 100%;
}

.settings {
  grid-area: settings;
  width: 100%;
  height: 100%;
}

.SizeOptions {
  width: 100%;
}

.InputImage {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}


.InputImageCanvasContainer {
  flex-grow: 0;
  display: block;
  text-align: center;
  width: 200px;
  height: 200px;
}

.CropEdit {
  margin-right: 32px;
}

.ColorSettingsEdit {
  margin-right: 32px;
}

h2 {
  text-align: center;
  /* background-color: ; */
}